.from-work .VillageView .feilds image {
    opacity: 0.3;
}

.VillageView .VillageName {
    text-align: right;
    max-width: 200px;
    overflow: hidden;
    margin-inline-start: 25px;
}

.VillageView svg.feilds, .VillageView svg.feilds>image {
    max-width: 300px;
    max-height: 264px;
    width: 300px;
    height: 264px;
}

.VillageView svg.feilds text {
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
}